/* eslint-disable jsx-a11y/label-has-for */

import Button from '@material-ui/core/Button';
import Container from '@material-ui/core/Container';
import { withStyles } from '@material-ui/core/styles';
import CloudUploadIcon from '@material-ui/icons/CloudUpload';
import PropTypes, { element } from 'prop-types';
import React from 'react';

const onClickFileSelector = event => {
  console.log(event.target);
  const img = document.getElementById('testImg');
  console.log(img);
  const reader = new FileReader();
  reader.onloadend = () => {
    img.src = reader.result;
  };
  reader.readAsDataURL(event.target.files[0]);
  console.log(reader);
  event.target.after(img);
};

const styles = {
  root: {
  },
  container: {
    maxWidth: 1440,
    flexWrap: 'wrap',
    display: 'flex',
    height: 800,
  },
  subContainer: {
    display: 'flex',
    alignSelf: 'center',
    marginTop: 50,
    height: 480,
    width: 480,
    outline: '5px dotted deeppink',
    outlineOffset: -50,
  },
  buttonContainer: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  uploadButton: {
  },
  uploadInput: {
    display: 'none',
  },
  rightIcon: {
    marginLeft: 8,
  },
};

const FileSelector = ({
  classes: {
    container,
    subContainer,
    buttonContainer,
    uploadButton,
    uploadInput,
    rightIcon,
  },
}) => (
  <Container maxWidth="xl" className={container}>
    <Container maxWidth="sm" className={subContainer}>
      <img id="testImg" />
      <Container maxWidth="sm" className={buttonContainer}>
        <input
          accept="image/*"
          className={uploadInput}
          id="outlined-button-file"
          type="file"
          onInput={onClickFileSelector}
        />
        <label htmlFor="outlined-button-file">
          <Button variant="contained" color="default" component="span" className={uploadButton}>
            Upload
            <CloudUploadIcon className={rightIcon} />
          </Button>
        </label>
      </Container>
    </Container>
    <Container maxWidth="sm" className={subContainer}>
      <Button variant="contained" color="default" className={uploadButton}>
        Upload
        <CloudUploadIcon className={rightIcon} onClick={onClickFileSelector} />
      </Button>
    </Container>
  </Container>
);

FileSelector.propTypes = {
  classes: PropTypes.shape({
    container: PropTypes.shape({}).isRequired,
    subContainer: PropTypes.shape({}).isRequired,
    buttonContainer: PropTypes.shape({}).isRequired,
    uploadButton: PropTypes.shape({}).isRequired,
    uploadInput: PropTypes.shape({}).isRequired,
    rightIcon: PropTypes.shape({}).isRequired,
  }).isRequired,
};

FileSelector.defaultProps = {
};

export default withStyles(styles)(FileSelector);
